import './Header.scss'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {images} from '../../assets'
import {useState, useEffect} from 'react'
import SignUp  from '../SignUp/SignUp'
import Cart  from '../Cart/Cart'
import ScrollToTop  from '../SrollToTop/ScrollToTop'
import { getDatabase, ref, child, get } from "firebase/database";
import { initializeApp } from "firebase/app";
/* import { getAnalytics } from "firebase/analytics"; */
function Header(){
  /* useEffect(()=>{
    const axios = require('axios').default;
    axios.get('https://food-delivery-app-8a5b6-default-rtdb.asia-southeast1.firebasedatabase.app/cart.json')
    .then(function (response) {
      console.log(response.data);
    })
    .catch(function (error) {
      // handle error
      console.log(error);
    })
  },[]) */

  const firebaseConfig = {
    apiKey: "AIzaSyC0u7J-uyBFqIfH_Dc4G68LVHHNIlioErA",
    authDomain: "food-delivery-app-8a5b6.firebaseapp.com",
    databaseURL: "https://food-delivery-app-8a5b6-default-rtdb.asia-southeast1.firebasedatabase.app",
    projectId: "food-delivery-app-8a5b6",
    storageBucket: "food-delivery-app-8a5b6.appspot.com",
    messagingSenderId: "1029670863493",
    appId: "1:1029670863493:web:09e226f4537ad4df0fdfec",
    measurementId: "G-QDMR833X15"
  };

  // Initialize Firebase
  const app = initializeApp(firebaseConfig);
  const database = getDatabase(app);

  const ref = getDatabase()
  console.log(ref);


  const $ = document.querySelector.bind(document)
  const $$ = document.querySelectorAll.bind(document)
  const [bars, setBars] = useState(true)
  const [count, setCount] = useState(0)
  const [cart, setCart] = useState(0)
  const [showScrollToTop, setShowScrollToTop] = useState(false)
  useEffect(()=>{ 
    window.addEventListener('scroll', () => {
      if(window.scrollY > 50){
        setShowScrollToTop(!showScrollToTop)
        $('.header').classList.add('fixed')
      }else{
        setShowScrollToTop(false)
        $('.header').classList.remove('fixed')
      }
    })
    return ()=>{
      window.removeEventListener('scroll')
    }
  },[])
  /* handle event onclick bars button */
  const handleBars = () =>{
    const barsAnimation = $('.bars svg').animate([{
      transform: 'rotate(180deg)'
    }],{
      duration: 200
    })
    $('.headerMenu').classList.toggle('active')
    setBars(!bars)
  }
  
  /* call sign up form */
  const handleCallSignUp = () =>{
    setCount(count + 1)
  }
  
  const handleCallCart = () =>{
    setCart(cart + 1)
  }

  console.log('rerender header');
  return<>
    {showScrollToTop && <ScrollToTop />}
    <SignUp count={count} />
    <Cart cart={cart}/>
    <header className="header grid wide">
      <div className="headerInner row">
        <div className=" col l-2">
          <div className="icon">
            <img src={images.Logo}/>
          </div>
        </div>
        <div className="headerMenu col l-6 m-6 ">
          <ul>
              <li><a /* href="" */>Home</a></li>
              <li><a /* href="" */>Review</a></li>
              <li><a /* href="" */>About</a></li>
              <li><a /* href="" */>Contact us</a></li>
          </ul>
        </div>
        <div className="col l-2">
            <div className="headerSignup">
              <div className="bars" onClick={handleBars}>
                {bars && <FontAwesomeIcon icon='bars'/>
                    || <FontAwesomeIcon icon='times'/>
                }
              </div>
              <div className="signUp"
                  onClick={handleCallSignUp}
              >
                  <span>Sign in</span>
              </div>
              <div className="cart"
                    onClick={handleCallCart}  
              >
                <FontAwesomeIcon icon='shopping-cart'/>
              </div>
            </div>
        </div>
      </div>
    </header>
  </>
}

export default Header